<template>
  <v-card raised elevation="12">
    <v-card-text>
      <h1 color="primary">Recuperar contraseña</h1>
    </v-card-text>
    <v-card-subtitle>Ingresa el correo electrónico con el cual te registraste</v-card-subtitle>
    <v-card-actions>
      <v-col>
        <v-form ref="form" v-model="valid">
            <v-text-field v-on:keydown.enter.prevent="resetPassword" type="email" label="Escribe tu correo electrónico" :rules="emailRules" outlined required v-model="email"></v-text-field>
            <v-btn type="button" :disabled="!valid" raised color="primary" @click="resetPassword" large block>Restablecer contraseña</v-btn>
        </v-form>
      </v-col>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapActions } from "vuex"

export default {
    data: function() {
        return {
            valid: true,
            email: null,
            reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
            emailRules: [
                v => !!v || 'El correo electrónico es requerido',
                v => this.reg.test(v) || 'El correo electrónico es inválido'
            ]
        };
    },
    methods: {
        ...mapActions("login", [
            "resetPasswordAction"
        ]),
        resetPassword () {
            if (this.$refs.form.validate()) {
                this.resetPasswordAction(this.email)
            }            
        }
  },
}
</script>